.logo {
  transition: all 0.3s ease 0s;

  @include vp-1023 {
    margin-top: 10px;
  }

  @include vp-767 {
    position: absolute;
    top: 10px;
    left: 15px;
  }

  &--footer {
    @include vp-767 {
      position: static;
    }
  }
}

.logo__link {
  color: inherit;
}

.logo__img {
  width: 100%;
  display: block;

  @include vp-1023 {
    width: 38px;
    height: 20px;
  }

  @include vp-767 {
    width: 42px;
    height: 22px;
  }
}
